@mixin mq-sm {
    @media ($mq-query-sm) {
        @content; } }
@mixin mq-md {
    @media ($mq-query-md) {
        @content; } }
@mixin mq-lg {
    @media ($mq-query-lg) {
        @content; } }
@mixin mq-xl {
    @media ($mq-query-xl) {
        @content; } }

@mixin mq-till-sm {
    @media ($mq-query-till-sm) {
        @content; } }
@mixin mq-till-md {
    @media ($mq-query-till-md) {
        @content; } }
@mixin mq-till-lg {
    @media ($mq-query-till-lg) {
        @content; } }
@mixin mq-till-xl {
    @media ($mq-query-till-xl) {
        @content; } }

@mixin mq-adaptive($separator: '-', $with-initial: true) {
    @if $with-initial {
        @content;
        &#{$separator}sm {
            @include mq-sm {
                @content; } }
        &#{$separator}md {
            @include mq-md {
                @content; } }
        &#{$separator}lg {
            @include mq-lg {
                @content; } }
        &#{$separator}xl {
            @include mq-xl {
                @content; } } } }
