
/* ---- cells ----*/
$cells: 12;

@function cell-width($index, $cells) {
    @return 100% / $cells * $index; }

.cell {
    padding: 0 16px;
    &-- {
        &space_none {
            @include mq-adaptive() {
                padding: 0; } }
        &display_flex {
            @include mq-adaptive() {
                display: flex;
                flex-wrap: wrap; } }

        @for $i from 1 through $cells {
            &#{$i} {
                $columns: cell-width($i, $cells);
                @include mq-adaptive() {
                    width: $columns; } } } } }

